package org.example.kmpnews.`data`.news

import app.cash.sqldelight.Query
import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlCursor
import app.cash.sqldelight.db.SqlDriver
import kotlin.Any
import kotlin.Boolean
import kotlin.String
import kotlinx.datetime.Instant

public class NewsDaoQueries(
  driver: SqlDriver,
  private val NewsEntityAdapter: NewsEntity.Adapter,
) : SuspendingTransacterImpl(driver) {
  public fun isExist(url: String): Query<Boolean> = IsExistQuery(url) { cursor ->
    cursor.getBoolean(0)!!
  }

  public fun <T : Any> getAllFavorite(mapper: (
    sourceName: String,
    author: String?,
    title: String,
    description: String?,
    url: String,
    urlToImage: String?,
    publishedAt: Instant,
    content: String?,
  ) -> T): Query<T> = Query(-1_011_589_834, arrayOf("NewsEntity"), driver, "NewsDao.sq",
      "getAllFavorite", "SELECT * FROM NewsEntity") { cursor ->
    mapper(
      cursor.getString(0)!!,
      cursor.getString(1),
      cursor.getString(2)!!,
      cursor.getString(3),
      cursor.getString(4)!!,
      cursor.getString(5),
      NewsEntityAdapter.publishedAtAdapter.decode(cursor.getLong(6)!!),
      cursor.getString(7)
    )
  }

  public fun getAllFavorite(): Query<NewsEntity> = getAllFavorite { sourceName, author, title,
      description, url, urlToImage, publishedAt, content ->
    NewsEntity(
      sourceName,
      author,
      title,
      description,
      url,
      urlToImage,
      publishedAt,
      content
    )
  }

  public suspend fun insertFavorite(
    sourceName: String,
    author: String?,
    title: String,
    description: String?,
    url: String,
    urlToImage: String?,
    publishedAt: Instant,
    content: String?,
  ) {
    driver.execute(777_963_396, """
        |INSERT OR REPLACE INTO
        |NewsEntity(sourceName, author, title, description, url, urlToImage, publishedAt, content)
        |VALUES (?,?,?,?,?,?,?,?)
        """.trimMargin(), 8) {
          bindString(0, sourceName)
          bindString(1, author)
          bindString(2, title)
          bindString(3, description)
          bindString(4, url)
          bindString(5, urlToImage)
          bindLong(6, NewsEntityAdapter.publishedAtAdapter.encode(publishedAt))
          bindString(7, content)
        }.await()
    notifyQueries(777_963_396) { emit ->
      emit("NewsEntity")
    }
  }

  public suspend fun deleteFavorite(url: String) {
    driver.execute(1_696_823_414, """DELETE FROM NewsEntity WHERE url = ?""", 1) {
          bindString(0, url)
        }.await()
    notifyQueries(1_696_823_414) { emit ->
      emit("NewsEntity")
    }
  }

  public suspend fun deleteAll() {
    driver.execute(1_573_939_847, """DELETE FROM NewsEntity""", 0).await()
    notifyQueries(1_573_939_847) { emit ->
      emit("NewsEntity")
    }
  }

  private inner class IsExistQuery<out T : Any>(
    public val url: String,
    mapper: (SqlCursor) -> T,
  ) : Query<T>(mapper) {
    override fun addListener(listener: Query.Listener) {
      driver.addListener("NewsEntity", listener = listener)
    }

    override fun removeListener(listener: Query.Listener) {
      driver.removeListener("NewsEntity", listener = listener)
    }

    override fun <R> execute(mapper: (SqlCursor) -> QueryResult<R>): QueryResult<R> =
        driver.executeQuery(348_641_982, """SELECT count(*) > 0 FROM NewsEntity WHERE url = ?""",
        mapper, 1) {
      bindString(0, url)
    }

    override fun toString(): String = "NewsDao.sq:isExist"
  }
}
