package org.example.kmpnews.dataModule

import app.cash.sqldelight.SuspendingTransacterImpl
import app.cash.sqldelight.db.AfterVersion
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import kotlin.Long
import kotlin.Unit
import kotlin.reflect.KClass
import org.example.kmpnews.MyDatabase
import org.example.kmpnews.`data`.news.NewsDaoQueries
import org.example.kmpnews.`data`.news.NewsEntity

internal val KClass<MyDatabase>.schema: SqlSchema<QueryResult.AsyncValue<Unit>>
  get() = MyDatabaseImpl.Schema

internal fun KClass<MyDatabase>.newInstance(driver: SqlDriver,
    NewsEntityAdapter: NewsEntity.Adapter): MyDatabase = MyDatabaseImpl(driver, NewsEntityAdapter)

private class MyDatabaseImpl(
  driver: SqlDriver,
  NewsEntityAdapter: NewsEntity.Adapter,
) : SuspendingTransacterImpl(driver), MyDatabase {
  override val newsDaoQueries: NewsDaoQueries = NewsDaoQueries(driver, NewsEntityAdapter)

  public object Schema : SqlSchema<QueryResult.AsyncValue<Unit>> {
    override val version: Long
      get() = 1

    override fun create(driver: SqlDriver): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
      driver.execute(null, """
          |CREATE TABLE NewsEntity (
          |--     id TEXT NOT NULL PRIMARY KEY,
          |    sourceName TEXT NOT NULL,
          |    author TEXT,
          |    title TEXT NOT NULL,
          |    description TEXT,
          |    url TEXT NOT NULL PRIMARY KEY,
          |    urlToImage TEXT,
          |    publishedAt INTEGER NOT NULL,
          |    content TEXT
          |)
          """.trimMargin(), 0).await()
    }

    override fun migrate(
      driver: SqlDriver,
      oldVersion: Long,
      newVersion: Long,
      vararg callbacks: AfterVersion,
    ): QueryResult.AsyncValue<Unit> = QueryResult.AsyncValue {
    }
  }
}
