package org.example.kmpnews

import app.cash.sqldelight.SuspendingTransacter
import app.cash.sqldelight.db.QueryResult
import app.cash.sqldelight.db.SqlDriver
import app.cash.sqldelight.db.SqlSchema
import kotlin.Unit
import org.example.kmpnews.`data`.news.NewsDaoQueries
import org.example.kmpnews.`data`.news.NewsEntity
import org.example.kmpnews.dataModule.newInstance
import org.example.kmpnews.dataModule.schema

public interface MyDatabase : SuspendingTransacter {
  public val newsDaoQueries: NewsDaoQueries

  public companion object {
    public val Schema: SqlSchema<QueryResult.AsyncValue<Unit>>
      get() = MyDatabase::class.schema

    public operator fun invoke(driver: SqlDriver, NewsEntityAdapter: NewsEntity.Adapter): MyDatabase
        = MyDatabase::class.newInstance(driver, NewsEntityAdapter)
  }
}
